<template lang="pug">
  .profile.inner-page
    .inner-page__main
      .profile__info
        profile-info(me online :info="getInfo")
      .profile__news(v-if="getWall.length > 0")
        .profile__tabs
          span.profile__tab(@click="changeTab('POSTED')" :class="{active: activeTab === 'published'}") Мои публикации ({{getAllActivePosts}})
          span.profile__tab(@click="changeTab('QUEUED')" :class="{active: activeTab === 'queue'}" v-if="getAllQueuedPosts > 0") Отложенные публикации ({{getAllQueuedPosts}})
        .profile__add
          news-add
        .profile__news-list
          news-block(edit deleted :deffered="activeTab === 'queued'" v-for="news in wallType" :key="news.id" :info="news")
          button.btn(@click="loadActivePosts" v-if="activeTab === 'POSTED' && wall.length < getAllActivePosts") ещё
          button.btn(@click="loadQueuedPosts" v-if="activeTab === 'QUEUED' && queuedWall.length < getAllQueuedPosts") ещё
    .inner-page__aside
      friends-request
      br
      friends-possible
</template>

<script>
import FriendsPossible from '@/components/Friends/Possible'
import FriendsRequest from '@/components/Friends/Request'
import ProfileInfo from '@/components/Profile/Info'
import NewsAdd from '@/components/News/Add'
import NewsBlock from '@/components/News/Block'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Profile',
  components: { FriendsPossible, FriendsRequest, ProfileInfo, NewsAdd, NewsBlock },
  data: () => ({
    activeTab: 'POSTED',
    wall: [],
    queuedWall: [],
    offset: 0,
    itemPerPage: 20,
  }),
  computed: {
    ...mapGetters('profile/info', ['getInfo']),
    ...mapGetters('users/info', ['getWall', 'getQueuedWall', 'getAllActivePosts', 'getAllQueuedPosts']),
    activeWall() {
      this.wall = [...new Set([...this.wall, ...this.getWall])];
      return this.wall
    },
    queWall() {
      this.queuedWall = [...new Set([...this.queuedWall, ...this.getQueuedWall])];
      return this.queuedWall
    },
    wallType() {
      this.offset = 0;
      return this.activeTab === "POSTED" ? this.activeWall : this.queWall
    }
  },
  methods: {
    ...mapActions('users/info', ['apiWall', 'apiQueuedWall']),
    changeTab(tab) {
      this.activeTab = tab
    },
    loadActivePosts() {
      this.offset += this.itemPerPage;
      this.apiWall({ id: this.getInfo.id, offset: this.offset, itemPerPage: this.itemPerPage });
    },
    loadQueuedPosts() {
      this.offset += this.itemPerPage;
      this.apiQueuedWall({ id: this.getInfo.id, offset: this.offset, itemPerPage: this.itemPerPage });

    },
  },
  created() {
    this.$store.commit('users/info/setResetWall', []);

    if (this.getInfo) this.apiWall({ id: this.getInfo.id, offset: this.offset, itemPerPage: this.itemPerPage });
    // if (this.getInfo) this.apiQueuedWall({ id: this.getInfo.id, offset: this.offset, itemPerPage: this.itemPerPage });
  },
}
</script>



